      <div id='persona-detail-show' v-if='editing === false && form.id != 0'>
      <h1>Detail</h1>
      <span> Persona ID: {{form.id}} | Revision: {{form.revision}}  </span>
      <div class="">
        <div class="">
          <img src="../../../public/assets/img_avatar2.png" alt="Avatar" class="avatar">
