<script>
/*eslint-disable */
import axios from 'axios'
import CommentView from '../CommentView.vue'
import {EventBus} from "../../index.js";

export default {
  name: "persona-details",
  components : {'comment-view': CommentView },
  data() {
    return {
      form: {
        id : null,
        name: '',
        title: '',
        external: '',
        market_size: '',
        quote: '',
        job_function: '',
        needs: '',
        wants: '',
        pain_point: '',
        buss_val: '',
        revision: '',
        product: [
           {product_id: '' , product_name:''},
         ],
        persona_picture: '',
        persona_file: null},
      editing: false,
      source: 'persona',
      options: [
          { value: 'EngineWise', text: 'EngineWise' },
          { value: 'PWX', text: 'PWX' },
          { value: 'Connected Factory', text: 'Connected Factory' },
        ]
      }
    },
    beforeMount() {
      const self = this;

      // SET OPTIONS
      axios.get("/api/products")
        .then(response => {
          self.options = response.data;
        })
        .catch(error => console.log(error))

      // UPDATE DATA ON CHANGES
      EventBus.$on('selection-changed', function(selection){

        var get_url = "/api/persona-table/";
        get_url += selection;

        axios.get(get_url)
        .then(response => {
            self.form.id = selection;
            self.form.name = response.data[0].name;
            self.form.title= response.data[0].title;
            self.form.external= response.data[0].external;
            self.form.market_size= response.data[0].market_size;
            self.form.quote = response.data[0].quote;
            self.form.job_function = response.data[0].job_function;
            self.form.needs = response.data[0].needs;
            self.form.wants = response.data[0].wants;
            self.form.pain_point= response.data[0].pain_point;
            self.form.buss_val= response.data[0].buss_val;
            self.form.revision= response.data[0].revision;
            self.form.product = response.data[0].product;
            self.editing = false;
          }
        )
        .catch(error => console.log(error))
        });
      },
      methods: {
       onEdit(evt) {

         axios({
             method: 'put',
             url: '/api/persona-table',
             data: this.form, })
         .then(function (response) {
             console.log(response);})
         .catch(function (error) {
             console.log(error);})

         EventBus.$emit('persona-table-changed','item-updated')
         document.getElementById("mySidepanel").style.width = "0px";
       },

       onAdd() {
         console.log(this.form)
         axios({
             method: 'post',
             url: '/api/persona-table',
             data: this.form, })
         .then(function (response) {
             console.log(response);})
         .catch(function (error) {
             console.log(error);})

         EventBus.$emit('persona-table-changed','item-updated');
         document.getElementById("right-sidepanel").style.width = "0px";
       },

       onReset() {
         // Reset our form values
         this.editing = false;
       },

       onArchive() {
         var get_url = '/api/persona-table/';
         get_url += this.form.id ;
         //get_url += '?name=frank';

         var archive_set = { 'archived': 1};

         console.log(get_url)
         axios({
             method: 'PUT',
             url: get_url,
             data: archive_set,
            })
         .then(function (response) {
             console.log(response);})
         .catch(function (error) {
             console.log(error);})

         console.log('delete')
         EventBus.$emit('persona-table-changed' , archive_set )
         document.getElementById("right-sidepanel").style.width = "0px";
      },
     },
  };

</script>


